import { notification } from 'ant-design-vue'
import configs from '@config'
// 
const PRODUCTION =
  process.env.VUE_APP_MODE === 'release' ||
  process.env.VUE_APP_MODE === 'production'

export default function errorHandler (error) {
  if (!error.response) {
    if (error.message && error.message.includes('timeout')) {
      console.error('')
      notification.error({
        message: '',
        description: ',',
        onClick: () => {}
      })
    } else if (error.message && error.message === '') {
      console.error('')
    } else if (error.message && error.message.includes('cancel')) {
      console.error('')
    } else {
      console.error('', error)
    }
  } else {
    configs.api.error_handler && configs.api.error_handler(error)
    if (!PRODUCTION) {
      let url = error.response.config.url
      let params = error.response.config.data
      let code = error.response.status
      let status = error.response.data.status
      notification.error({
        message: '',
        duration: 0,
        description: h => (
          <div style="width:6rem;padding:.2rem;word-break: break-all">
            url<p>{url}</p><p>{params}</p><p>{code}</p>
            <p>{status}</p>
          </div>
        ),
        onClick: () => {}
      })
    }
  }
}
