import router from '@router'
import utils from '@utils'
import qs from 'qs'
console.log(
  '%c ' + process.env.NODE_ENV,
  'font-size:14px;color:#fff;background:red;padding:4px;font-family:Artiely;letter-spacing:2px'
)
console.log(
  '%c ' + process.env.VUE_APP_MODE,
  'font-size:14px;color:#fff;background:red;padding:4px;font-family:Artiely;letter-spacing:2px'
)

const MODE = process.env.VUE_APP_MODE
let BASE_URL = ''
switch (MODE) {
  case 'dev':
    // 
    BASE_URL = 'api/'
    break
  case 'test':
    // 
    BASE_URL = 'api/'
    break
  case 'production':
    // 
    BASE_URL = 'api/'
    break
  case 'release':
    // 
    BASE_URL = 'api/'
    break
}
export default {
  // api
  api: {
    retry: 1, // 
    retryDelay: 1000, // 
    shouldRetry: () => true, // 
    url: BASE_URL,
    timeout: 1000,
    //   
    header: {
      'Cache-Control': 'no-cache',
      'Content-type': 'application/x-www-form-urlencoded'
    },
    // ()
    set_config: config => {
      config.data = qs.stringify(config.data, {
        arrayFormat: 'indices',
        allowDots: true
      })
      return config
    },
    //   
    error_handler: error => {
      const responseCode = error.response.data.status
      switch (responseCode) {
        case 4:
          router.replace({
            name: 'login',
            params: {
              message: ''
            }
          })
          utils.removeToken()
          break
        default:
      }
    },
    //   
    before_fetch: () => {
      if (!navigator.onLine) {
        this.$message.warning('')
      }
    }
  },
  //   
  router_auth: true,
  router_before_each: (to, from, next) => {
    // let { token } = getToken()
    // if (to.meta.auth) {
    //   // 
    //   if (token) {
    //     next({
    //       query: {
    //         redirect: to.fullPath
    //       }
    //     })
    //   } else {
    //     next({
    //       path: '/login',
    //       query: {
    //         redirect: to.fullPath
    //       }
    //     })
    //   }
    // } else {
    //   next()
    // }
    next()
  }
}
