import moment from 'moment'
// 
/**
 *
 * String day 
 * String week 
 * String month 
 * String prevMonth 
 * String season 
 * String prevSeason 
 * String year 
 * String prevYear 
 */
export function timeRange (timeType) {
  let startTime
  let endTime
  switch (timeType) {
    case 'day':
      startTime =
        moment()
          .startOf('day')
          .format('YYYY-MM-DD') + ' 00:00:00'
      endTime = moment().format('YYYY-MM-DD') + ' 23:59:59'
      break
    case 'week':
      startTime =
        moment()
          .startOf('week')
          .format('YYYY-MM-DD') + ' 00:00:00'
      endTime = moment().format('YYYY-MM-DD') + ' 23:59:59'
      break
    case 'month':
      startTime =
        moment()
          .startOf('month')
          .format('YYYY-MM-DD') + ' 00:00:00'
      endTime = moment().format('YYYY-MM-DD') + ' 23:59:59'
      break
    case 'prevMonth':
      startTime =
        moment()
          .subtract(1, 'month')
          .startOf('month')
          .format('YYYY-MM-DD') + ' 00:00:00'
      endTime =
        moment()
          .subtract(1, 'month')
          .endOf('month')
          .format('YYYY-MM-DD') + ' 23:59:59'
      break
    case 'year':
      startTime =
        moment()
          .startOf('year')
          .format('YYYY-MM-DD') + ' 00:00:00'
      endTime = moment().format('YYYY-MM-DD') + ' 23:59:59'
      break
  }
  return [startTime, endTime]
}
