/**
 * 
 * 
 * __noCancel
 * B
 */
import axios from 'axios'
import qs from 'qs'
import errorHandler from './error-handler'
import configs from '@config'
import { message } from 'ant-design-vue'

const CancelToken = axios.CancelToken
// 
let requestMap = new Map()

axios.defaults.retry = configs.api.retry // 
axios.defaults.retryDelay = configs.api.retryDelay // 
axios.defaults.shouldRetry = configs.api.shouldRetry // 

window.addEventListener('offline', function (e) {
  message.warning('')
})

export default function fetch (options, argu) {
  configs.api.before_fetch && configs.api.before_fetch()
  let headers = Object.assign(
    {
      'Cache-Control': 'no-cache',
      'Content-type': 'application/x-www-form-urlencoded'
    },
    configs.api.headers
  )
  let timeout = configs.api.timeout || 10000
  const instance = axios.create({
    baseURL: configs.api.url,
    headers: headers,
    validateStatus: function (status) {
      return status === 200
    },
    responseType: 'json',
    responseEncoding: 'utf8', // default
    // `transformResponse`  then/catch 
    transformResponse: [
      data => {
        // 
        if (data) {
          return data
        } else {
          return {
            status: 500
          }
        }
      }
    ],
    timeout: timeout, // default is `0` (no timeout)
    withCredentials: false // default
  })

  /**
   * 
   * 
   */

  instance.interceptors.request.use(
    config => {
      // ()
      //  key
      const keyString = qs.stringify(Object.assign({}, { url: config.url }))
      // 
      if (requestMap.has(keyString)) {
        //  message
        requestMap.get(keyString)('', config)
        //  Map  key  catch
      }
      // cancel token
      config.cancelToken = new CancelToken(cancel => {
        // __noCancel
        if (!config.data.__noCancel) {
          requestMap.set(keyString, cancel)
        }
      })

      Object.assign(config, { _keyString: keyString })

      let cusConfig = configs.api.set_config(config)

      return { ...config, ...cusConfig }
    },
    error => {
      return Promise.reject(error)
    }
  )

  /**
   * 
   * 
   */

  instance.interceptors.response.use(
    response => {
      const responseCode = response.status
      // 200
      // 
      if (responseCode === 200) {
        return Promise.resolve(response)
      } else {
        return Promise.reject(response)
      }
    },
    error => {
      var config = error.config
      // 
      if (!config || !config.retry) {
        errorHandler(error)
        return Promise.reject(error)
      }

      if (!config.shouldRetry || typeof config.shouldRetry !== 'function') {
        errorHandler(error)
        return Promise.reject(error)
      }

      // 
      if (!config.shouldRetry(error)) {
        errorHandler(error)
        return Promise.reject(error)
      }

      // 0
      config.__retryCount = config.__retryCount || 0

      // 
      if (config.__retryCount >= config.retry) {
        errorHandler(error)
        return Promise.reject(error)
      }

      // 
      config.__retryCount += 1

      // 
      var backoff = new Promise(function (resolve) {
        setTimeout(function () {
          resolve()
        }, config.retryDelay || 1)
      })

      // axios
      return backoff.then(function () {
        return axios(config)
      }).catch(e => {
        errorHandler(e)
      })
    }
  )

  // 
  return new Promise((resolve, reject) => {
    instance(options)
      .then(res => {
        resolve(res.data)
        return false
      })
      .catch(error => {
        reject(error)
      })
  })
}
